.sobre {
	background-color: #f8f7ff;
	text-align: center;
	padding: 100px 20px;
}
.sobre p {
	line-height: 30px;
}
.sobre .card,
.sobre img {
	width: 50%;
	max-height: 300px;
	border: #a2adad solid 1px;
}
.sobre img {
	object-fit: cover;
}
.sobre .card-group {
	width: 100%;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}
.sobre .card {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background-color: #fff;
	padding: 20px 25px;
}
.sobre .card h1 {
	font-size: 36px;
	font-family: "Anybody", cursive;
	letter-spacing: 5px;
	line-height: 42px;
}
.sobre .card p {
	font-size: 22px;
	color: #4a4a4a;
}
.sobre img {
	object-fit: fit;
}

@media (max-width: 768px) {
	.sobre .card {
		padding: 15px;
	}
	.sobre .card h1 {
		font-size: 22px;
	}
	.sobre .card p {
		font-size: 16px;
	}
}
@media (max-width: 550px) {
	.sobre {
		padding: 100px 10px;
	}
	.sobre .card-group {
		flex-direction: column;
	}
	.sobre .card,
	.sobre img {
		width: 100%;
		height: 100%;
	}
}
